import Goods from "./components/goods";
import NoGoods from "./components/noGoods";
export default {
  components: {
    Goods,
    NoGoods
  },
  data() {
    return {
      isEdit: false,
      selectList: [1, 2],
      goodsList: [
        {
          id: 1,
          state: "无货",
          _isChecked: true,
          image:
            "http://img3.imgtn.bdimg.com/it/u=999277307,2658085126&fm=11&gp=0.jpg"
        },
        {
          id: 2,
          _isChecked: false,
          image:
            "http://img3.imgtn.bdimg.com/it/u=999277307,2658085126&fm=11&gp=0.jpg"
        },
        {
          id: 3,
          _isChecked: false,
          image:
            "http://img3.imgtn.bdimg.com/it/u=999277307,2658085126&fm=11&gp=0.jpg"
        },
        {
          id: 4,
          _isChecked: false,
          image:
            "http://img3.imgtn.bdimg.com/it/u=999277307,2658085126&fm=11&gp=0.jpg"
        },
        {
          id: 5,
          _isChecked: false,
          image:
            "http://img3.imgtn.bdimg.com/it/u=999277307,2658085126&fm=11&gp=0.jpg"
        }
      ]
    };
  },
  async onReady() {},
  onLoad(options) {
    uni.setNavigationBarTitle({
      title: "浏览记录"
    });
  },
  methods: {
    handlerEdit() {
      this.isEdit = true;
    },
    handlerSave() {
      this.isEdit = false;
    },
    handlerDelete() {},
    handlerSelect(item) {
      let itemData = this.goodsList.find(val => val.id === item.id);
      item._isChecked.detail = !item._isChecked.detail;
      console.log(this.selectList, item._isChecked, itemData._isChecked);
    },
    selectChange() {
      console.log(this.selectList);
    },
    selectAll() {}
  }
};
